// import logo from './logo.svg';
import "./App.css";
import { createTheme, ThemeProvider } from "@mui/material";

// React 
import { useEffect, useState } from "react";



// Material UI Components
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import CloudIcon from '@mui/icons-material/Cloud';
import Button from '@mui/material/Button';

// External Libraries Axios
import axios from "axios";
import moment from "moment";
import "moment/min/locales";
import { useTranslation } from 'react-i18next';
moment.locale("ar");

const theme = createTheme({
  typography: {
    fontFamily: "IBM",
    fontWeightLight: 300,
    fontWeightRegular: 400,
    fontWeightMedium: 500,
    fontWeightBold: 700,
  },
});

let cancelAxios = null

function App() {
  const { t, i18n } = useTranslation();

  // ==== States =====//
  const [data, setData] = useState("");
  const [temp, setTemp] = useState({
    number: null,
    description: "",
    min: "",
    max: "",
    icon: null
  });

  const [locale, setLocale] = useState("ar")

  const direction = locale === "ar" ? "rtl" : "ltr"

  // ====== Event Handlers =====

  function handleLanguageClick() {
    if (locale === "en") {
      setLocale("ar");
      i18n.changeLanguage("ar");
      moment.locale("ar");
    } else {
      setLocale("en");
      i18n.changeLanguage("en");
      moment.locale("en");
    }
    setData(moment().format('MMMM Do YYYY, h:mm:ss a'));
  };


  useEffect(() => {
    i18n.changeLanguage(locale);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {

    setData(moment().format('MMMM Do YYYY, h:mm:ss a'));
    axios
      .get("https://api.openweathermap.org/data/2.5/weather?lat=30.03&lon=31.23&appid=d2344117ad407bc2598340cb92ec75df",
        {
          cancelToken: new axios.CancelToken((c) => {
            cancelAxios = c;
          }),
        }
      )
      .then((response) => {
        // handle success
        const responseTemp = Math.round(response.data.main.temp - 272.15);
        const min = Math.round(response.data.main.temp_min - 272.15);
        const max = Math.round(response.data.main.temp_max - 272.15);
        const description = response.data.weather[0].description;
        const responseIcon = response.data.weather[0].icon;
        console.log(responseTemp, min, max, description, responseIcon);

        setTemp({
          number: responseTemp,
          description: description,
          min: min,
          max: max,
          icon: `https://openweathermap.org/img/wn/${responseIcon}@2x.png`
        });
      })
      .catch((error) => {
        // handle error
        console.log(error);
      });

    return () => {
      console.log("unmounting the components");
      cancelAxios();
    };
  }, []);
  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <Container maxWidth="sm">
          {/* Content Container */}
          <div
            style={{
              height: "100vh",
              padding: "20px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              flexDirection: "column"
            }}
          >
            {/* == Content Container == */}
            {/* Card */}
            <div
              dir={direction}
              style={{
                width: "100%",
                background: "rgb(28 52 91 / 36%)",
                borderRadius: "15px",
                padding: "10px",
                boxShadow: "0px 11px 1px rgba(0, 0, 0, 0.5)",
                color: "White"
              }}
            >
              {/* Card Content */}
              <div>
                {/* City & Time */}
                <div
                  style={{
                    display: "flex",
                    alignItems: "end",
                    justifyContent: "start",
                  }}
                  dir={direction}
                >
                  <Typography
                    variant="h2"
                    style={{
                      fontWeight: "600",
                      marginRight: "10px"
                    }}
                  >
                    {t("Cairo")}
                  </Typography>

                  <Typography
                    variant="h5"
                    style={{
                      marginRight: "15px",
                    }}
                  >
                    {data}
                  </Typography>
                </div>
                {/* == City & Time ==*/}

                <hr />
                {/* Container Of Degree + Cloud Icon */}
                <div style={{
                  display: "flex",
                  justifyContent: "space-around"
                }}>
                  {/* Degree & Description */}
                  <div>
                    {/* Temp */}
                    <div style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center"
                    }}>
                      <Typography
                        variant="h1"
                        style={{
                          textAlign: "right"
                        }}
                      >
                        {temp.number}
                      </Typography>
                      <img src={temp.icon} alt="clear sky icon" />
                    </div>
                    {/*== Temp ==*/}
                    <Typography
                      variant="h6"
                    >
                      {t(temp.description)}
                    </Typography>

                    {/* Min & Max */}
                    <div style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center"
                    }}>
                      <h5>{t("min")} : {temp.min}</h5>
                      <h5 style={{ margin: "0px 5px" }}> | </h5>
                      <h5>{t("max")} : {temp.max}</h5>
                    </div>
                    {/*== Min & Max ==*/}

                  </div>
                  {/* == Degree & Description == */}

                  <CloudIcon style={{
                    fontSize: "200px",
                    color: "white"
                  }} />
                </div>
                {/* == Container Of Degree + Cloud Icon ==*/}
              </div>
              {/* == Card Content ==*/}
            </div>
            {/* == Card == */}

            {/* Translation Container */}
            <div dir={direction} style={{
              width: "100%",
              display: "flex",
              justifyContent: "end",
              marginTop: "20px"
            }}>
              <Button
                style={{ color: "white" }}
                variant="text" onClick={handleLanguageClick}>
                {locale === "en" ? "Arabic" : "إنجليزي"}
              </Button>
            </div>
            {/* Translation Container */}
          </div>
        </Container>
      </ThemeProvider>
    </div>
  );
}

export default App;
